// utils.js

// Generate a random integer between min and max (inclusive)
export const getRandomInt = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

// Structured positioning with controlled randomness
export const generateStructuredPosition = (index, totalItems, size, containerSize, randomnessFactor = 0.3) => {
  const padding = 100; // Minimum distance from edges
  const minSpacing = size * 1.5; // Minimum spacing between bubbles
  
  // Create a flexible grid based on screen size and number of items
  const cols = Math.ceil(Math.sqrt(totalItems));
  const rows = Math.ceil(totalItems / cols);
  
  // Calculate grid cell dimensions
  const cellWidth = (containerSize.width - padding * 2) / cols;
  const cellHeight = (containerSize.height - padding * 2) / rows;
  
  // Get grid position
  const row = Math.floor(index / cols);
  const col = index % cols;
  
  // Base position (center of grid cell)
  const baseX = padding + col * cellWidth + cellWidth / 2 - size / 2;
  const baseY = padding + row * cellHeight + cellHeight / 2 - size / 2;
  
  // Add controlled randomness
  const maxOffset = Math.min(cellWidth, cellHeight) * randomnessFactor;
  const offsetX = getRandomInt(-maxOffset, maxOffset);
  const offsetY = getRandomInt(-maxOffset, maxOffset);
  
  // Ensure position stays within bounds
  const x = Math.max(padding, Math.min(containerSize.width - size - padding, baseX + offsetX));
  const y = Math.max(padding, Math.min(containerSize.height - size - padding, baseY + offsetY));
  
  return { x, y };
};

// Enhanced positioning for small bubbles around large bubbles
export const generateSmallBubblePositions = (centerX, centerY, largeSize, smallBubbles, existingPositions) => {
  const positions = [];
  const radius = largeSize / 2 + 60; // Distance from center of large bubble
  const minAngleStep = (2 * Math.PI) / Math.max(smallBubbles.length, 6); // Minimum angle between bubbles
  
  smallBubbles.forEach((bubble, index) => {
    // Calculate base angle with equal distribution
    const baseAngle = index * minAngleStep;
    
    // Add small random variation to angle for natural look
    const angleVariation = (Math.random() - 0.5) * (minAngleStep * 0.3);
    const angle = baseAngle + angleVariation;
    
    // Add random radius variation
    const radiusVariation = (Math.random() - 0.5) * 30;
    const finalRadius = radius + radiusVariation;
    
    // Calculate position
    const x = centerX + Math.cos(angle) * finalRadius;
    const y = centerY + Math.sin(angle) * finalRadius;
    
    positions.push({
      x: Math.max(0, x),
      y: Math.max(0, y),
      size: 50, // Standard small bubble size
      angle: angle
    });
  });
  
  return positions;
};

export const generateNonOverlappingPosition = (size, existingPositions, containerSize, originalPosition) => {
  let position;
  let overlap;
  const maxAttempts = 2000;
  let attempts = 0;

  console.log(`Generating non-overlapping position for size ${size} within container ${containerSize.width}x${containerSize.height}`);

  do {
    overlap = false;

    let xmax = containerSize.width - size;
    let ymax = containerSize.height - size;
    
    position = {
      x: getRandomInt(0, xmax),
      y: getRandomInt(0, ymax)
    };

    for (let existing of existingPositions) {
      if (Math.abs(position.x - existing.x) < size && Math.abs(position.y - existing.y) < size) {
        overlap = true;
        break;
      }
    }

    console.log(`Generated position: ${position.x}, ${position.y} after ${attempts} attempts for large bubble at ${originalPosition.x}, ${originalPosition.y}, ymax is ${ymax}, xmax is ${xmax}, ymin is ${originalPosition.y}, xmin is ${originalPosition.x}`);

    attempts++;
  } while (overlap && attempts < maxAttempts);


  return {position, overlap};
};

export const fetchPositionsFromFile = async (filename) => {
  const response = await fetch(`${process.env.PUBLIC_URL}/position/${filename}`);
  if (!response.ok) {
    throw new Error('Network response was not ok');
  }
  return await response.json();
};

// utils.js
export const generateBezierPath = (positions) => {
  if (positions.length < 2) return '';

  // Sort positions based on the x-coordinate
  const sortedPositions = [...positions].sort((a, b) => a.x - b.x);

  const path = sortedPositions.reduce((acc, pos, index) => {
    const { x, y } = pos;
    if (index === 0) {
      return `M${x},${y}`;
    } else {
      const cp1X = (sortedPositions[index - 1].x + x) / 2;
      const cp1Y = sortedPositions[index - 1].y;
      const cp2X = (sortedPositions[index - 1].x + x) / 2;
      const cp2Y = y;
      return `${acc} C${cp1X},${cp1Y} ${cp2X},${cp2Y} ${x},${y}`;
    }
  }, '');

  return path;
};
