import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import SmallBubble from './SmallBubble';
import { generateSmallBubblePositions, fetchPositionsFromFile } from './utils';

const pulse = keyframes`
  0% {
    transform: scale(1);
    box-shadow: 0 0 5px rgba(218, 255, 251, 0.5);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px rgba(218, 255, 251, 1);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 5px rgba(218, 255, 251, 0.5);
  }
`;

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
`;

const Bubble = styled.div.attrs(props => ({
  style: {
    width: `${props.size}px`,
    height: `${props.size}px`,
    top: `${props.position.y}px`,
    left: `${props.position.x}px`,
    backgroundImage: `url(${props.image})`,
  }
}))`
  background-size: cover;
  background-position: center;
  font-size: ${props => Math.max(14, props.size / 8)}px;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
  color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  transition: all 0.4s ease;
  animation: ${pulse} 3s infinite, ${fadeIn} 0.6s ease-out;
  border: 3px solid rgba(255, 255, 255, 0.3);
  text-align: center;
  padding: 10px;
  
  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 30px rgba(218, 255, 251, 0.8);
    border-color: rgba(255, 255, 255, 0.6);
  }
  
  &:active {
    transform: scale(0.95);
  }
`;

const SmallBubblesContainer = styled.div`
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 1000;
`;
const HighlightArea = styled.div`
  position: absolute;
  width: 200px;
  height: 200px;
  border: 3px dashed rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  animation: ${pulse} 4s infinite;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  color: #7469B6;
  z-index: 1001;
  transition: all 0.3s ease;
  
  &:hover {
    background: rgba(255, 255, 255, 1);
    transform: scale(1.1);
  }
`;

const LargeBubble = ({ skill, subSkills, image, size, position, existingPositions, onSmallBubblePositionsGenerated }) => {
  const [expanded, setExpanded] = useState(false);
  const [smallBubblePositions, setSmallBubblePositions] = useState([]);

  useEffect(() => {
    if (expanded && subSkills && subSkills.length > 0) {
      // Use the new structured positioning for small bubbles
      const centerX = position.x + size / 2;
      const centerY = position.y + size / 2;
      
      const positions = generateSmallBubblePositions(
        centerX,
        centerY,
        size,
        subSkills,
        existingPositions
      );
      
      setSmallBubblePositions(positions);
      
      // Notify parent component about the new positions
      if (onSmallBubblePositionsGenerated) {
        onSmallBubblePositionsGenerated(positions);
      }
    }
  }, [expanded, subSkills, size, position, existingPositions, onSmallBubblePositionsGenerated]);

  const handleClick = () => {
    setExpanded(true);
  };

  const handleClose = (e) => {
    e.stopPropagation();
    setExpanded(false);
    setSmallBubblePositions([]);
  };

  return (
    <>
      {!expanded && (
        <Bubble onClick={handleClick} size={size} position={position} image={image}>
          {skill}
        </Bubble>
      )}
      {expanded && (
        <SmallBubblesContainer>
          <HighlightArea style={{
            top: position.y + size / 2,
            left: position.x + size / 2
          }} />
          <CloseButton onClick={handleClose}>
            ×
          </CloseButton>
          {smallBubblePositions.length > 0 && subSkills.map((subSkill, index) => (
            <SmallBubble 
              key={`${subSkill}-${index}`} 
              skill={subSkill} 
              position={smallBubblePositions[index]} 
            />
          ))}
        </SmallBubblesContainer>
      )}
    </>
  );
};

export default LargeBubble;
