import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import LargeBubble from './LargeBubble';
import bubble from './bubble.png';
import { generateStructuredPosition, fetchPositionsFromFile } from './utils';

const Container = styled.div`
  position: relative;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(#FFE6E6,#E1AFD1,#AD88C6);
  overflow: hidden;
  font-family: "Ubuntu Sans Mono", monospace;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  color: #7469B6;
  
  /* Add subtle animation to the background */
  background-size: 200% 200%;
  animation: gradientShift 10s ease infinite;
  
  @keyframes gradientShift {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
`;

const LoadingIndicator = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #7469B6;
  font-size: 1.2em;
  text-align: center;
`;

const skills = [
  {
    skill: 'Artificial Intelligence',
    subSkills: ['Data Analysis', 'Computer Vision', 'PyTorch', 'Deep Learning', 'Natural Language Processing'],
    image: bubble,
    priority: 1 // High priority skills appear first
  },
  {
    skill: 'Web Development',
    subSkills: ['HTML', 'CSS', 'JavaScript', 'React', 'Node.js'],
    image: bubble,
    priority: 1
  },
  {
    skill: 'Software Development',
    subSkills: ['Java', 'Python', 'C++', 'Data Structure and Algorithm', 'Git', 'Docker'],
    image: bubble,
    priority: 2
  },
  {
    skill: 'Game Development',
    subSkills: ['Unity', 'Unreal Engine', 'C#', 'Mechanic Design', 'Narrative Design'],
    image: bubble,
    priority: 2
  },
  {
    skill: 'Virtual Reality',
    subSkills: ['Unity', 'VRChat SDK', 'Oculus XR Tools', 'User Experience Design'],
    image: bubble,
    priority: 3
  },
];


export function Skill() {
  const [positions, setPositions] = useState([]);
  const [allSmallBubblePositions, setAllSmallBubblePositions] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const loadPositions = async () => {
      setIsLoading(true);
      
      try {
        const containerSize = { 
          width: window.innerWidth - 100, 
          height: window.innerHeight - 100 
        };
        
        // Sort skills by priority for better positioning
        const sortedSkills = [...skills].sort((a, b) => a.priority - b.priority);
        
        const newPositions = [];
        let useStructured = true;
        
        // Try structured positioning first
        sortedSkills.forEach((skillSet, index) => {
          const size = Math.max(120, skillSet.subSkills.length * 35 + 80); // More reasonable sizing
          const position = generateStructuredPosition(
            index, 
            sortedSkills.length, 
            size, 
            containerSize,
            0.25 // Reduced randomness for better structure
          );
          
          newPositions.push({ ...position, size });
        });
        
        // Check for overlaps and adjust if needed
        const minDistance = 180; // Minimum distance between bubble centers
        for (let i = 0; i < newPositions.length; i++) {
          for (let j = i + 1; j < newPositions.length; j++) {
            const dist = Math.sqrt(
              Math.pow(newPositions[i].x - newPositions[j].x, 2) + 
              Math.pow(newPositions[i].y - newPositions[j].y, 2)
            );
            
            if (dist < minDistance) {
              // Spread them apart
              const angle = Math.atan2(
                newPositions[j].y - newPositions[i].y,
                newPositions[j].x - newPositions[i].x
              );
              
              const targetDist = minDistance + 20;
              newPositions[j].x = newPositions[i].x + Math.cos(angle) * targetDist;
              newPositions[j].y = newPositions[i].y + Math.sin(angle) * targetDist;
              
              // Keep within bounds
              newPositions[j].x = Math.max(50, Math.min(containerSize.width - newPositions[j].size - 50, newPositions[j].x));
              newPositions[j].y = Math.max(50, Math.min(containerSize.height - newPositions[j].size - 50, newPositions[j].y));
            }
          }
        }
        
        // Reorder positions to match original skills array order
        const finalPositions = skills.map(skill => {
          const sortedIndex = sortedSkills.findIndex(s => s.skill === skill.skill);
          return newPositions[sortedIndex];
        });
        
        setPositions(finalPositions);
        
      } catch (error) {
        console.error('Error generating positions, falling back to file:', error);
        try {
          const storedPositions = await fetchPositionsFromFile('largePos.json');
          setPositions(storedPositions);
        } catch (fallbackError) {
          console.error('Failed to fetch fallback positions:', fallbackError);
          // Generate simple fallback positions
          const fallbackPositions = skills.map((_, index) => ({
            x: 100 + (index % 3) * 300,
            y: 100 + Math.floor(index / 3) * 250,
            size: 150
          }));
          setPositions(fallbackPositions);
        }
      } finally {
        setIsLoading(false);
      }
    };

    loadPositions();
    
    // Regenerate on window resize
    const handleResize = () => {
      loadPositions();
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleSmallBubblePositions = (positions) => {
    setAllSmallBubblePositions(prev => [...prev, ...positions]);
  };

  if (isLoading) {
    return (
      <Container>
        <LoadingIndicator>
          Loading Skills...
        </LoadingIndicator>
      </Container>
    );
  }

  return (
    <Container>
      {positions.length > 0 && skills.map((skillSet, index) => (
        <LargeBubble
          key={`${skillSet.skill}-${index}`}
          skill={skillSet.skill}
          subSkills={skillSet.subSkills}
          image={skillSet.image}
          size={positions[index]?.size || 150}
          position={{ 
            x: positions[index]?.x || 100, 
            y: positions[index]?.y || 100 
          }}
          existingPositions={allSmallBubblePositions}
          onSmallBubblePositionsGenerated={handleSmallBubblePositions}
        />
      ))}
    </Container>
  );
}

export default Skill;