import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import InfoBox from './InfoBox';

const popIn = keyframes`
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
`;

const float = keyframes`
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
`;

const Bubble = styled.div.attrs(props => ({
  style: {
    width: `${props.position.size}px`,
    height: `${props.position.size}px`,
    top: `${props.position.y}px`,
    left: `${props.position.x}px`,
  }
}))`
  background: linear-gradient(135deg, #E1F7F5, #64CCC5);
  border: 2px solid #E1F7F5;
  border-radius: 50%;
  font-size: ${props => Math.max(10, props.position.size / 4)}px;
  font-weight: 600;
  color: #2D5A87;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  cursor: pointer;
  transition: all 0.4s ease;
  animation: ${popIn} 0.6s ease-out, ${float} 3s ease-in-out infinite;
  box-shadow: 0 4px 15px rgba(100, 204, 197, 0.3);
  z-index: 1001;
  text-align: center;
  padding: 5px;
  pointer-events: auto;
  
  /* Stagger animation delay based on position */
  animation-delay: ${props => (props.position.angle || 0) * 0.1}s;
  
  &:hover {
    transform: scale(1.15) translateY(-5px);
    box-shadow: 0 8px 25px rgba(100, 204, 197, 0.6);
    background: linear-gradient(135deg, #64CCC5, #E1F7F5);
    border-color: #64CCC5;
  }
  
  &:active {
    transform: scale(0.95);
  }
`;

const SmallBubble = ({ skill, position }) => {
  const [hovered, setHovered] = useState(false);

  return (
    <>
      <Bubble
        position={position}
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
      >
        {skill}
      </Bubble>
      {hovered && (
        <InfoBox
          title={skill}
          content={`Details about ${skill}`}
          position={position}
        />
      )}
    </>
  );
};

export default SmallBubble;
